html {
  background-color: #6d15ae;
  color: #fff;
  font-family: "Proxima Nova";
  line-height: 1.1;
}

body {
  margin: 0;
  padding: 0;
}

a {
  color: #fff;
  font-weight: bold;
}

@-webkit-keyframes focus-outline-pulse {
  from {
    outline-width: 7px;
  }

  to {
    outline-width: 12px;
  }
}

@keyframes focus-outline-pulse {
  from {
    outline-width: 7px;
  }

  to {
    outline-width: 12px;
  }
}

:focus {
  outline: #a03fff auto 7px;
  outline-offset: -3px;
  -webkit-animation-duration: 800ms;
          animation-duration: 800ms;
  -webkit-animation-name: focus-outline-pulse;
          animation-name: focus-outline-pulse;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}



