.line-through {
  text-decoration: line-through;
}

main {
  background-color: #fff;
  color: #6416b0;
}

main a {
  color: #6416b0;
}

.logo {
  font-weight: bold;
  font-size: 30px;
  padding: 13px 0px;
  padding-right: 26px;
  text-transform: lowercase;
}

.logo span {
  margin-top: -2px;
  display: inline-block;
}

.logo img {
  margin-right: 12px;
}

.logo span,
.logo img {
  vertical-align: middle;
}

nav {
  border-top: 2px solid #fff;
  background-color: #fff;
  -webkit-box-shadow: 0px 3px 0px 0px #4f1c870d, 0px 1px 1px 0px #4f1c870d;
          box-shadow: 0px 3px 0px 0px #4f1c870d, 0px 1px 1px 0px #4f1c870d;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 1;
  -webkit-transition: border-top-color 200ms linear;
  -o-transition: border-top-color 200ms linear;
  transition: border-top-color 200ms linear;
}

.home-page nav {
  border-top: 2px solid #6416b0;
  -webkit-border-image: -webkit-linear-gradient(306deg, #6416b0, #dd0699) 10;
       -o-border-image: -o-linear-gradient(306deg, #6416b0, #dd0699) 10;
          border-image: linear-gradient(144deg, #6416b0, #dd0699) 10;
}

nav .nav-gutter {
  display: -ms-flexbox;
  display: flex;
  max-width: 1170px;
  margin: 0 auto;
  -ms-flex-align: center;
      align-items: center;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

nav ul li {
  -ms-flex: 1 1;
      flex: 1 1;
}

nav ul li a {
  padding: 25px 10px;
  padding-bottom: 26px;
  text-decoration: none;
  display: block;
  font-weight: normal;
  position: relative;
}

nav ul li.active a:after {
  content: '';
  display: block;
  position: absolute;
  height: 3px;
  background: #6416b0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.nav-spacer {
  -ms-flex: 1 1;
      flex: 1 1;
}

.nav-social .twitter-follow-button,
.nav-social .github-button {
  visibility: hidden;
}

.nav-social > * {
  vertical-align: middle;
}

.nav-social > * + * {
  margin-left: 10px;
}

header {
  background: -webkit-linear-gradient(306deg, #6416b0, #dd0699);
  background: -o-linear-gradient(306deg, #6416b0, #dd0699);
  background: linear-gradient(144deg, #6416b0, #dd0699);
  -webkit-box-shadow: inset 0px 0px 24px #ff00b133, inset 6px 10px 60px #6416b052;
          box-shadow: inset 0px 0px 24px #ff00b133, inset 6px 10px 60px #6416b052;
  padding: 100px;
  padding-top: 169px;
  max-width: 1400px;
  margin: 0 auto;
  text-align: center;
}

header h1 {
  font-size: 25px;
  margin: 0;
  margin-bottom: 40px;
  color: #fff;
}

header h1 span:first-child {
  opacity: 0.7;
}

header h1 span:last-child {
  font-size: 46px;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(40%, #fff), color-stop(60%, #ffe9f6));
  background-image: -webkit-linear-gradient(top, #fff 40%, #ffe9f6 60%);
  background-image: -o-linear-gradient(top, #fff 40%, #ffe9f6 60%);
  background-image: linear-gradient(180deg, #fff 40%, #ffe9f6 60%);
  color: #fff;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  position: relative;
  padding: 0 1px;
  z-index: 0;
}

header h1 span:last-child:before {
  background: none;
  content: "Reactive Programming Languuge";
  left: 0;
  position: absolute;
  text-shadow: 1px 5px 0px #6416b01f;
  top: 0;
  z-index: -1;
}

.install-button {
  text-decoration: none;
  color: #6416b0;
  margin: 0 auto;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(40%, #fff), color-stop(60%, #fff3fa));
  background-image: -webkit-linear-gradient(top, #fff 40%, #fff3fa 60%);
  background-image: -o-linear-gradient(top, #fff 40%, #fff3fa 60%);
  background-image: linear-gradient(180deg, #fff 40%, #fff3fa 60%);
  display: inline-block;
  font-size: 26px;
  font-family: "Proxima Nova";
  font-weight: bold;
  border: none;
  border-radius: 3px;
  padding: 22px 40px;
  -webkit-box-shadow: 1px 5px 0px #6416b01f, 0px 2px 0px 2px #6416b0;
          box-shadow: 1px 5px 0px #6416b01f, 0px 2px 0px 2px #6416b0;
  margin-bottom: 4px;
}

footer {
  padding: 23px 0;
  text-align: center;
  font-size: 12px;
}



